@import './variables.scss';
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');
@import './button.scss';

* {
  font-family: "Roboto Condensed", sans-serif; // Устанавливаем для всей страницы шрифты
  margin: 0; // Убираем отступы
  padding: 0; // Убираем отступы
  box-sizing: border-box; // Устанавливаем начальные настройки поведения боксов
}

ul,
li,
dl {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  &:hover {
      text-decoration: none;
      color: inherit;
  }
}

p {
  margin: 0;
  padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; 
}

.app {
  width: 1100px;
  margin: 0 auto;
  padding: 50px 0 50px 0;

  .bg-decoration {
      position: absolute;
      right: -174px;
      bottom: -50px;
      z-index: -1;
  }
}


main {
  margin-top: 50px;
  position: relative;
}

.pulse {
  animation: pulse 1.5s ease-in-out .5s infinite
}

@keyframes pulse {
  0% {
      opacity: 1
  }
  50% {
      opacity: .4
  }
  100% {
      opacity: 1
  }
}